.modal {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
  background: rgba(0, 0, 0, 0.9);
  padding: 5vw;

  .con {
    width: 100%;
    height: 100%;
    overflow: hidden;

    video,
    iframe {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border: none;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
  .close {
    position: absolute;
    top: 30px;
    right: 30px;
    font: bold 14px/1;
    color: orange;
    cursor: pointer;
  }
}
